







:root {
  --footer-bg-color: #9bb537;
  --footer-text-color: #dcee92;
  --footer-link-color: #292424;
  --footer-link-bg-color: #cfcece;
  --btn-to-top-bg-color: #f68787;
  --btn-to-top-fill-color: #5ba016;
  --focus-visible-box-shadow-color: rgba(0, 0, 0, .5);
}

.site-footer {
  background-color: var(--footer-bg-color);
  color: var(--footer-text-color);
  padding: 20px 0;
}

.footer-content {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.footer-section {
  text-align: center;
  padding: 10px;
}

.f-list {
  justify-content: space-between;
  gap: 20px;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.footer-media-item {
  margin: 0;
}

.footer-media-link {
  color: var(--footer-link-color);
  width: 40px;
  height: 40px;
  background-color: var(--footer-link-bg-color);
  color: var(--footer-text-color);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  transition: color .3s, background-color .3s;
  display: flex;
}

.footer-media-icon {
  width: 24px;
  height: 24px;
  fill: currentColor;
}

.footer-media-link:hover, .footer-media-link:focus {
  background-color: var(--footer-text-color);
  outline: none;
}

.footer-media-link:focus-visible {
  box-shadow: 0 0 0 3px var(--focus-visible-box-shadow-color);
}

.footer-media-title {
  color: var(--footer-text-color);
  text-align: center;
  margin-bottom: 16px;
  font-size: 18px;
}

#btn-to-top {
  width: 50px;
  height: 50px;
  background-color: var(--btn-to-top-bg-color);
  fill: var(--btn-to-top-fill-color);
  cursor: pointer;
  opacity: .6;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  transition: opacity .3s;
  display: none;
  position: fixed;
  bottom: 30px;
  right: 30px;
}

#btn-to-top.is-visible {
  display: flex;
}

#btn-to-top:hover {
  opacity: 1;
}

@media screen and (min-width: 768px) {
  .footer-content {
    flex-direction: row;
    justify-content: space-between;
  }

  .footer-section {
    text-align: left;
  }

  .f-list {
    gap: 30px;
  }
}

@media screen and (min-width: 1280px) {
  .footer-content {
    padding: 0 40px;
  }

  .footer-section {
    padding: 15px;
  }
}

.hero-container {
  min-width: 0;
  padding: 0 15px;
}

.hero-slider_image {
  text-align: center;
}

.hero-slider_image img {
  max-width: 100%;
}

.hero-slider .swiper-pagination {
  bottom: 0;
}

.hero-slider .swiper-pagination-bullet {
  background-color: #9bb537;
}

.hero-slider .name-master-class {
  position: absolute;
  bottom: 0;
}

.hero-slider_wrapper-text {
  flex-direction: column;
  align-items: center;
  gap: 8px;
  display: flex;
  position: absolute;
  bottom: 0;
}

.hero-slider .text-region {
  color: rgba(248, 248, 248, .2);
  font-size: 8px;
  line-height: 12px;
}

.hero-slider .name-master-class {
  color: rgba(248, 248, 248, .6);
  font-size: 10px;
  line-height: 1.4;
}

.hero-title {
  text-transform: uppercase;
  max-width: 620px;
  font-size: 26px;
  line-height: 1.2;
}

.hero-title-green {
  color: #9bb537;
  font-style: italic;
  font-size: inherit;
  line-height: inherit;
}

.hero-text {
  color: #050505;
  max-width: 380px;
  margin-top: 14px;
  font-size: 14px;
  line-height: 1.29;
}

.btnOrderNow {
  width: 112px;
  height: 36px;
  font: inherit;
  color: inherit;
  cursor: pointer;
  text-align: center;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #9bb537;
  border-radius: 12px;
  outline: none;
  margin-top: 28px;
  margin-bottom: 48px;
  font-size: 14px;
  font-weight: 700;
}

.btnOrderNow:hover, .btnOrderNow:focus {
  color: #f8f8f8;
  background-color: #9bb537;
  border: none;
}

.swiper-container {
  width: 496px;
  height: 302px;
  background-color: #0ff;
  margin-bottom: 80px;
  display: block;
}

@media screen and (min-width: 768px) {
  .hero-container {
    min-width: 0;
    padding: 0 32px;
  }

  .hero-title {
    width: 447px;
    font-size: 36px;
  }

  .hero-title-green {
    font-size: inherit;
  }

  .hero-text {
    width: 384px;
    color: #050505;
    margin-top: 16px;
    font-size: 16px;
    line-height: 1.5;
  }

  .btnOrderNow {
    width: 127px;
    height: 45px;
    border-radius: 15px;
    margin-top: 32px;
    margin-bottom: 64px;
  }

  .swiper-container {
    width: 871px;
    height: 468px;
    background-color: #0ff;
    margin-bottom: 80px;
    display: block;
  }
}

@media screen and (min-width: 1280px) {
  .hero-container {
    gap: 32px;
    display: flex;
  }
}











body {
  color: #050505;
  background-color: #f8f8f8;
  font-family: Inter;
  font-weight: 500;
}

.container {
  width: 100%;
  margin: 0 auto;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
  }
}

html {
  scroll-behavior: smooth;
}

